@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Rounded:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200');    

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-size: 100%;
  display: block;

  --fs-xl: clamp(5rem, 3vw + 1rem, 10rem);
  --fs-600: calc(0.3vw + 1.4rem);
  --fs-700: 2rem;
  --fs-400: 1.225rem;
}

@media (min-width: 70em) {
  :body{
    --fs-600: 3rem;
    --fs-400: 1.125rem;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p{
  font-size: var(--fs-600);
  line-height: 1.25em;
}

h1{
  font-size: var(--fs-xl);
}

h2{
  font-size: var(--fs-700);
}

.App {
  background-color:#801414;
  min-height: 100vh;
  justify-content: center;
  color: white;
}

/* contact box */
@media (max-height: 600px) {
  .contact{
    display: none;
  }
  .slide-out{
    display: none;
  }
}

.contact {
  text-align: center;
  width: 200px;
  height: 60px;
  position: fixed;
  bottom: 35px;
  left: 35px;
  padding: 8px;
  overflow: hidden;
  border: none;
  -webkit-border-radius: 130px;
  border-radius: 100px;
  color: rgba(255,255,255,1);
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  background: #640000;
  z-index: 10000;
}

.slide-out {
  text-align: center;
  width: 185px;
  height: 52px;
  max-width: 300px;
  max-height: 86px;
  position: fixed;
  bottom: 35px;
  left: 266px;
  padding: 8px;
  overflow: hidden;
  border: solid 4px #640000;
  -webkit-top-right-border-radius:140px;
  -webkit-bottom-right-border-radius:140px;
  border-top-right-radius: 140px;
  border-bottom-right-radius: 140px;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  color: rgba(255,255,255,1);
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  background: white;
  background-color: #fff;
  transition: transform 0.5s ease-in-out;
  z-index: 10000;
}

#contactP::before{
  content: url(images/telephone.png);
  position: absolute;
  left: -5px;
  bottom: 0px;
}

#contactP{
  max-width: 200px;
  max-height: 40px;
  position: absolute;
  font-family: Arial;
  font-size: 20px;
  font-weight: bolder;
  top: 43px;
  left: 61%;
  transform: translate(-50%, -50%);
  width: 180px;
  height: 30px;
  display: block;
  margin: auto;
}

#phone-number{
  max-width: 300px;
  max-height: 50px;
  width: auto;
  height: 25px;
  position: absolute;
  color: #820000;
  margin: 0;
  left: 45px;
  text-align: center;
  top: 7px;
  bottom: 5px;
  font-size: 20px;
  font-weight: bolder;
}

#phone-number2{
  max-width: 300px;
  max-height: 50px; 
  width: auto;
  height: 25px;
  position: absolute;
  color: #820000;
  margin: 0;
  left: 30px;
  text-align: center;
  bottom: 12px;
  font-size: 20px;
  font-weight: bolder;
}

.contact1:hover .slide-out {
  transform: translateX(-25%);
}
.contact1:not(:hover) .slide-out {
  transform: translateX(-100%);
}

/* navigation Bar */
.nav-bar{
  font-size:x-large;
  box-sizing: border-box;
  background-color: #720000;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  height: 4.2rem;
  width: 100%;
  z-index: 1000;
  position: fixed;
}

.nav-bar-ul{
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 2rem;
}

.nav-bar-a {
  --b: 0.1em;
  --c: #fff;
  
  color: #fff;
  text-decoration: none;
  padding-block: var(--b);
  background: 
    linear-gradient(#000 50%,#fff 0) 0% calc(100% - var(--_p,0%))/100% 200%,
    linear-gradient(#000 0 0) 0% var(--_p,0%)/var(--_p,0%) var(--b) no-repeat;
  -webkit-background-clip: text,padding-box;
          background-clip: text,padding-box;
  transition: .3s var(--_s,0s) linear,background-size .3s calc(.3s - var(--_s,0s));
}

.nav-bar a:hover {
  --_p: 100%;
  --_s: .3s;
  color: #000;
}

#logo{
  height: 100%;
  display: block;
  box-sizing: border-box;
  padding: 10px;
  zoom: 80%;
  z-index: 2000;
}

/* navigation Box */

@media (max-width: 1100px) {
  .nav-bar-ul{
    display: none;
  }
}

@media (min-width: 1100px) {
  .nav-button span{
    display: none;
  }
  .nav-button{
    display: none;
    position: relative;
    right: 100px;
  }
}

.nav-box {
  --nav-width: 100%;

  position: fixed;
  text-decoration: none;
  top: 0;
  padding: 0;
  right: 0;
  margin: 0;
  width: var(--nav-width);
  height: 450px;
  background: rgba(180, 0, 0, 0.4);
  transform: translateY(calc(-1 * var(--nav-width)));
  transition: transform 0.4s;
  backdrop-filter: blur(5px);
  z-index: 1000;
}

.nav-box-open {
  transform: translateY(0);
}

.nav-box-a{
  font-size: 125%;
  text-decoration: none;
}

.nav-box li{
  text-align: center;
  list-style: none;
  padding-top: 0px;
  padding-bottom: 20px;
}

.nav-box ul{
  padding-top: 60px;
}

.nav-overlay {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0, 0.1);
  transform: translateX(300px);
  transition: transform 0.25s ease-in-out;
}

.nav-overlay-open {
  display: block;
  transform: translateX(0);
} 

.material-symbols-rounded{
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 48
}

.nav-button{
  width: 70px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 1001;
}

.nav-button > span {
  color: #ffffff;
  font-size: 3.5rem;
  -webkit-text-size-adjust: 100%;
}

.nav-button span:hover {
  color: #D0B8A8;
}

#nav-div{
  justify-content: center;
  align-items: center;
  display: flex;
  z-index: 2000;
}

